@use "styles/stylesFunctions.scss" as stylesFunctions;

.body {
  background-color: #f8f8f8;
  padding-top: 98px;
  padding-bottom: 208px;
}
.mainRow {
  row-gap: 70px;
}
.pageHeaders {
  h3,
  p {
    margin: 0;
    text-align: center;
    width: 100%;
  }
  h3 {
    @include stylesFunctions.fontStyles(70px, 52px, 600, #000000, #0000);
  }
  p {
    @include stylesFunctions.fontStyles(35px, 26px, 300, #000000, #0000);
    opacity: 0.5;
 margin-top: 20px;
  }
}

.formRow {
  padding: 60px 50px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 1px 6px #00000029;
  border: 0.5px solid #707070;
  border-radius: 10px;
}

.spliterCol {
  position: relative;
  &::before {
    position: absolute;
    content: " ";
    width: 1px;
    height: 100%;
    background-color: #d2d2d2;
    top: 0;
    left: 35%;
  }
}
.Form {
  @include stylesFunctions.flexLayout(center, flex-start, column, 20px);
  h3,
  label,
  span,
  p {
    margin: 0;
  }
  h3 {
    @include stylesFunctions.fontStyles(45px, 34px, 600, #000000, #0000);
  }
  label {
    width: 100%;
    div {
      margin-top: 9px;
      @include stylesFunctions.flexLayout(space-between, center, row, initial);
      a {
        text-decoration: underline;
        color: #ffaa03;
      }
    }
  }

  input[type="password"],
  input[type="email"] {
    padding: 22px 31px;
    width: 100%;
    border-radius: 5px;
    border: solid 0.5px #d2d2d2;
    @include stylesFunctions.fontStyles(39px, 29px, 300, #000000, #0000);
  }
  input[type="checkbox"] {
    margin: 7px 5px;
    @include stylesFunctions.fontStyles(24px, 18px, 300, #000000, #0000);
  }
  input[type="submit"] {
    background-color: #001736;
    border-radius: 5px;
    width: 100%;
    padding-top: 22px;
    padding-bottom: 22px;
    @include stylesFunctions.fontStyles(29px, 22px, 600, #ffffff, #0000);
  }
}

.otherOptions {
  @include stylesFunctions.flexLayout(center, center, column, 20px);
  h5,
  p {
    margin: 0;
    width: 75%;
  }
  h5 {
    @include stylesFunctions.fontStyles(35px, 26px, 600, #000000, #0000);
    text-align: center;
  }
  p {
    @include stylesFunctions.fontStyles(26px, 20px, 300, #000000, #0000);
    color: #000000;
    opacity: 0.5;
    text-align: center;
  }
  .signUp {
    width: 75%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 5px;
    border: solid 1px #001736;
    text-align: center;
    @include stylesFunctions.fontStyles(25px, 24px, 600, #000000, #0000);
  }
  .loginOptions {
    @include stylesFunctions.flexLayout(center, center, row, 26px);
    img {
      width: 65px;
      aspect-ratio: 1/1;
    }
  }
}
